body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-raleway: Raleway;

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-lg: 18px;
  --font-size-xl: 20px;
  --font-size-3xl: 22px;
  --font-size-base: 16px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-darkslateblue-100: #57549e;
  --color-darkslateblue-200: #252360;

  /* Paddings */
  --padding-12xl: 31px;
  --padding-9xl: 28px;
  --padding-3xl: 22px;
  --padding-xl: 20px;

  /* border radiuses */
  --br-3xs: 10px;
  --br-26xl: 45px;
}
